import React from "react";
import ReactDOM from "react-dom";
import axios from "axios";
import { Form, Button } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import { Spinner, SpinnerSize } from "@fluentui/react/lib/Spinner";
import {
  DefaultButton,
  PrimaryButton,
  Stack,
  IStackTokens,
} from "@fluentui/react";
import ReactDiffViewer from "react-diff-viewer";
import { TextField } from "@fluentui/react/lib/TextField";
import {
  ComboBox,
  IComboBox,
  IComboBoxOption,
  SelectableOptionMenuItemType,
} from "@fluentui/react/lib/index";

import {
  Dropdown,
  DropdownMenuItemType,
  IDropdownStyles,
  IDropdownOption,
} from "@fluentui/react/lib/Dropdown";

class Input extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      arrayResult: [],
    };

    this.resolve = this.resolve.bind(this);
  }

  componentDidMount() {}

  resolve = (nestedObj, pathArr) => {
    console.log(nestedObj);
    return pathArr.reduce(
      (obj, key) => (obj && obj[key] !== "undefined" ? obj[key] : undefined),
      nestedObj
    );
  };

  render() {
    console.log(this.props.dataelastic);
    let bottone = "";
    let spin = "";
    let rr = "";
    let newCode = "";
    let arrayResult = [];
    let test = "";
    let valore1 = "";

    const dropdownStyles = {
      dropdown: { width: 300 },
    };

    const options = [
      {
        key: "WorkingTitle",
        text: "WorkingTitle",
        stringa: ["WorkingTitle"],
        stringaautonony: ["WorkingTitle"],
      },
      {
        key: "Name",
        text: "Name",
        stringa: ["name"],
        stringaautonony: ["name"],
      },
      {
        key: "id",
        text: "id",
        stringa: ["id"],
        stringaautonony: ["id"],
      },
    ];

    return (
      <div>
        <Dropdown
          placeholder="Select an option"
          label="Basic uncontrolled example"
          options={options}
          styles={dropdownStyles}
          onChange={this.props.myComboBoxChangeHandler}
          defaultSelectedKey={this.props.Voce}
        />

        <div>
          <Form onSubmit={this.props.handleSubmit}>
            <Form.Group controlId="formBasicEmail">
              {/*<Form.Label>Query name {this.props.queryname}</Form.Label>
              <Form.Control
                value={this.props.queryname}
                name="name"
                onChange={this.props.myChangeHandler}
                type="text"
                placeholder="Enter id"
              />
              <Form.Text className="text-muted">
                Inserire id della query
              </Form.Text>*/}

              <TextField
                label="Code Query"
                name="query"
                multiline
                autoAdjustHeight
                onChange={this.props.myChangeHandlerCodeQuery}
              />
            </Form.Group>

            <PrimaryButton text="Submit" type="submit" name="ricerca" />
          </Form>
          <br />
          <br />
          <Form onSubmit={this.props.handleSubmitTry}>
            <PrimaryButton text="Try" type="submit" name="try" />
          </Form>
        </div>
        {/*
        <div className="p-2">
         
          {spin}
          <ul className="list-group">
                {bottone}
          </ul>
        </div>
        */}
        {spin}
        <div>
          <br />
        </div>
        <ReactDiffViewer
          oldValue={this.props.valore1}
          newValue={this.props.valore}
          splitView={true}
        />
      </div>
    );
  }
}
export default Input;
