import React, { Component } from 'react';
import axios from 'axios';

class Autonomy extends React.Component {
   
    constructor(props) {
        super(props);
       
      };

        componentDidMount() {
            console.log('componentDidMount Autonomy')
          
            axios.get('/'+this.props.path+'/'+this.props.queryname+'.txt')
            .then(res => {
              const persons = res.data;
              this.setState({ persons });
            })
        }
        render() {
            

            return (
                <div>
                    <h1>Autonomy {this.props.path}</h1>
                </div>
            );
        }
    }

export default Autonomy;